input {
  width: 200px;
  height: 45px;
  font-size: 20px;
}

button {
  width: 100px;
  height: 45px;
  font-size: 20px;
}

.timer {
  width: 800px;
  height: 250px;
  background-color: #fff8dc;
  border: 5px solid #b22222;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 10px;
  margin-top: 50px;
  padding: 20px;
  display: flex;
}

.field {
  width: 100%;
  min-width: 82px;
  height: 100%;
  background-color: #e8bb4b;
  border: 2px solid rgba(45, 44, 44, .604);
  border-radius: 4%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  display: flex;
}

.value, .label {
  text-align: center;
  font-size: 42px;
}

